import React, { useState } from 'react'
import { Row, Col, InputGroup, FormControl, Button } from 'react-bootstrap'
import Resists from '../Resists/resists'
import ChainAttack from './chain.attack'
import Buffs from '../Buffs/buffs'

export default function Chain({ attackChain, setAttackChain, archtype, secondary }) {
  const [forced, setForced] = useState(true)
  const [startTime, setStartTime] = useState(0)
  const [smashingLethal, setSmashingLethal] = useState(0.4033)
  const [fireCold, setFireCold] = useState(0.4255)
  const [energyNegative, setEnergyNegative] = useState(0.3984)
  const [toxicPsionic, setToxicPsionic] = useState(0.388)
  const [showDeBuffs, setShowDeBuffs] = useState(false)
  const [showRes, setShowRes] = useState(false)
  const [showBuffs, setShowBuffs] = useState(false)
  const [showTiming, setShowTiming] = useState(false)
  const [globalDamage, setGlobalDamage] = useState(0)
  const [defOG, setDefOG] = useState(0)
  const [corrOG, setCorrOG] = useState(0)
  const [defFort, setDefFort] = useState(0)
  const [corrFort, setCorrFort] = useState(0)
  const [defPB, setDefPB] = useState(0)
  const [corrPB, setCorrPB] = useState(0)
  const [defWoP, setDefWoP] = useState(0)
  const [corrWoP, setCorrWoP] = useState(0)
  const [defEmpCircuit, setDefEmpCircuit] = useState(0)
  const [corrEmpCircuit, setCorrEmpCircuit] = useState(0)
  const [toxins, setToxins] = useState(false)

  return (
    <div key={'chain' + archtype} className='justify-content-around mb-3 bg-dark p-3 text-white fw-bolder customBoxShadow'>
      {forced ? <></> : <div></div>}
      <Row key={'chain title'} className='justify-content-center '>
        <h3 className='text-center'>Attack Chain</h3>
      </Row>
      <Row key={'late by'} className='mt-2'>
        <Col xs={6} md={3} className='mt-2 d-flex justify-content-center '>
          <Button className='w-75  fw-bolder text-center' onClick={() => setShowDeBuffs(!showDeBuffs)}>
            {showDeBuffs ? 'Hide' : 'Show Debuffs'}
          </Button>
        </Col>
        <Col xs={6} md={3} className='mt-2 d-flex justify-content-center '>
          <Button className='w-75  fw-bolder text-center' onClick={() => setShowRes(!showRes)}>
            {showRes ? 'Hide' : `Res: ${(smashingLethal * 100).toFixed(2)}%`}
          </Button>
        </Col>
        <Col xs={6} md={3} className='mt-2 d-flex justify-content-center '>
          <Button className='w-75  fw-bolder text-center' onClick={() => setShowBuffs(!showBuffs)}>
            {showBuffs ? 'Hide' : 'Buffs'}
          </Button>
        </Col>
        <Col xs={6} md={3} className='mt-2 d-flex justify-content-center '>
          <Button className='w-75  fw-bolder text-center' onClick={() => setShowTiming(!showTiming)}>
            {showTiming ? 'Hide' : 'Edit Time'}
          </Button>
        </Col>
      </Row>
      {showDeBuffs && (
        <Resists
          key={'resists' + archtype}
          smashingLethal={smashingLethal}
          setSmashingLethal={setSmashingLethal}
          fireCold={fireCold}
          setFireCold={setFireCold}
          energyNegative={energyNegative}
          setEnergyNegative={setEnergyNegative}
          toxicPsionic={toxicPsionic}
          setToxicPsionic={setToxicPsionic}
        />
      )}
      {showBuffs && (
        <Buffs
          globalDamage={globalDamage}
          setGlobalDamage={setGlobalDamage}
          archtype={archtype}
          secondary={secondary}
          defOG={defOG}
          setDefOG={setDefOG}
          corrOG={corrOG}
          setCorrOG={setCorrOG}
          defFort={defFort}
          setDefFort={setDefFort}
          corrFort={corrFort}
          setCorrFort={setCorrFort}
          defPB={defPB}
          setDefPB={setDefPB}
          corrPB={corrPB}
          setCorrPB={setCorrPB}
          defWoP={defWoP}
          setDefWoP={setDefWoP}
          corrWoP={corrWoP}
          setCorrWoP={setCorrWoP}
          defEmpCircuit={defEmpCircuit}
          setDefEmpCircuit={setDefEmpCircuit}
          corrEmpCircuit={corrEmpCircuit}
          setCorrEmpCircuit={setCorrEmpCircuit}
          toxins={toxins}
          setToxins={setToxins}
        />
      )}
      {showTiming && (
        <Col xs={12} md={3} className='mt-2 d-flex justify-content-center'>
          <InputGroup className='btnShadow w-50 lateby'>
            <InputGroup.Text className='w-50 bg-secondary text-black fw-bolder text-center'>Late by: </InputGroup.Text>
            <FormControl style={{ backgroundColor: 'white' }} className='text-center' defaultValue={startTime} onChange={(e) => setStartTime(parseFloat(e.target.value))} />
          </InputGroup>
        </Col>
      )}
      <Row key={'chain headers'} className='mt-2 border-bottom d-flex align-items-center chain'>
        <Col className='p-2 text-center'>Name</Col>
        <Col className='p-2 text-center'>Cast At</Col>
        <Col className='p-2 text-center'>Distance From Target</Col>
        <Col className='p-2 text-center'>Hit Time</Col>
        {/* <Col className="p-2 text-center">Proc Rate</Col> */}
        <Col className='p-2 text-center'>Show Damage</Col>
        <Col className='p-2 text-center'>Remove Attack</Col>
      </Row>
      {attackChain.map((attack, index) => {
        let totalCast = 0.0
        totalCast += startTime
        for (let i = 0; i < index; i++) {
          totalCast += attackChain[i].castTime
        }
        return (
          <ChainAttack
            key={'chainAttackIndex' + attack.name}
            globalDamage={globalDamage}
            index={index}
            forced={forced}
            setForced={setForced}
            setAttackChain={setAttackChain}
            attackChain={attackChain}
            archtype={archtype}
            attackInit={attack}
            totalCast={totalCast}
            smashingLethal={smashingLethal}
            fireCold={fireCold}
            energyNegative={energyNegative}
            toxicPsionic={toxicPsionic}
            toxins={toxins}
          />
        )
      })}
    </div>
  )
}
